const themes = {
  dark: {
    bg: "#222",
    bgAlt: "#111",
    color: "rgba(255,255,255,0.85)",
    accent: "#89aec6"
  },
  light: {
    bg: "#fafafa",
    bgAlt: "#cfcfcf",
    color: "rgba(0,0,0,0.85)",
    accent: "#c68989"
  }
}


export default themes



